body {
  background-color: #f0f0f0;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
  overflow: hidden;
}

.stopwatch__center {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__hand {
  transform-origin: 50% 100%;
  z-index: 1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.stopwatch__hand--second {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-second;
}

.stopwatch__hand--minute {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-minute;
}

.stopwatch--speed-up .stopwatch__hand--second {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__hand--minute {
  animation-duration: 600s;
}

@keyframes rotate-second {
  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

@keyframes rotate-minute {
  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}
